	module.exports = {"data":{"post":{"html":"<p>Hamburger Abendblatt: <a href=\"http://www.abendblatt.de/daten/2002/10/28/86026.html\">500 000 Hamburger guckten in die Röhre</a>. They were expecting 100,000 to come see the new Elbtunnel. It turned out to be 500,000, and it was absolute chaos as the shuttle buses and ferries were unable to keep up, And even today, with the new tube open for traffic, there was still 18 km backed up for morning rush hour, since there are still only 2 lanes free in each direction. And the impatient try their luck with the Elbe Bridge, which was then backed up for 12 km. That's where I was stuck, which I only mention because after crawling in traffic for over an hour this morning, I mistimed a red light downtown. In the intersection saw the flash of the cameras on both sides of my car after it turned red. I can only hope that I was within the 2 second tolerance, or that the cameras were out of film. Or both.</p>","fields":{"title":"Tunnel Vision","date":"29 Oct 2002","url":"https://papascott.de/2002-10-29-tunnel-vision/"}}},"pathContext":{"slug":"/2002-10-29-tunnel-vision/","prev":{"fields":{"title":"Up All Night with Jeanett","date":"28 Oct 2002","slug":"/2002-10-28-up-all-night-with-jeanett/"}},"next":{"fields":{"title":"Cold Shoulder","date":"30 Oct 2002","slug":"/2002-10-30-cold-shoulder/"}}}}

